// Helper function to sort POs by shipment order within delivery windows
const sortPOsByShipment = (poList) => {
  return poList.sort((a, b) => {
    const shipmentA = getShipmentForPO(a);
    const shipmentB = getShipmentForPO(b);
    return shipmentA - shipmentB;
  });
};

