import React, { useState } from 'react';
import {
  SpaceBetween,
  Container,
  Header,
  Table,
  Box,
  Select,
  Input,
  Button
} from '@cloudscape-design/components';
import ShipmentCards from './ShipmentCards';
import { deliveryWindowsData, getShipmentForPO, shipmentColors, shipmentsData } from '../data';

const Option4Interactive = () => {
  const [highlightedShipment, setHighlightedShipment] = useState(null);
  const [hoveredPO, setHoveredPO] = useState(null);
  const [tooltip, setTooltip] = useState({ visible: false, x: 0, y: 0, content: '' });

  const renderPurchaseOrders = (purchaseOrders) => {
    return (
      <div style={{ display: 'flex', flexWrap: 'wrap', gap: '4px', alignItems: 'center' }}>
        {purchaseOrders.map((po, index) => {
          const shipmentId = getShipmentForPO(po);
          const isHighlighted = highlightedShipment === shipmentId || hoveredPO === po;
          const shipment = shipmentsData.find(s => s.id === shipmentId);
          
          return (
            <React.Fragment key={po}>
              <span
                style={{
                  backgroundColor: isHighlighted ? `${shipmentColors[shipmentId]}20` : 'transparent',
                  border: isHighlighted ? `1px solid ${shipmentColors[shipmentId]}` : '1px solid transparent',
                  borderRadius: '4px',
                  padding: '2px 6px',
                  cursor: 'pointer',
                  transition: 'all 0.2s ease',
                  fontSize: '14px',
                  color: '#0f141a',
                  display: 'inline-block'
                }}
                onMouseEnter={(e) => {
                  setHoveredPO(po);
                  setHighlightedShipment(shipmentId);
                  
                  // Show tooltip
                  const rect = e.target.getBoundingClientRect();
                  setTooltip({
                    visible: true,
                    x: rect.left + rect.width / 2,
                    y: rect.top - 10,
                    content: `Shipment ${shipmentId} (ARN: ${shipment?.arn || 'Unknown'})`
                  });
                }}
                onMouseLeave={() => {
                  setHoveredPO(null);
                  setHighlightedShipment(null);
                  setTooltip({ visible: false, x: 0, y: 0, content: '' });
                }}
              >
                {po}
              </span>
              {index < purchaseOrders.length - 1 && (
                <span style={{ color: '#0f141a', fontSize: '14px' }}>,</span>
              )}
            </React.Fragment>
          );
        })}
      </div>
    );
  };

  const columnDefinitions = [
    {
      id: 'dateRange',
      header: 'Delivery window date',
      cell: item => item.dateRange,
      width: 200
    },
    {
      id: 'totalPOs',
      header: 'Total POs',
      cell: item => item.totalPOs,
      width: 100
    },
    {
      id: 'purchaseOrders',
      header: 'Purchase Orders',
      cell: item => renderPurchaseOrders(item.purchaseOrders)
    }
  ];

  return (
    <div style={{ position: 'relative' }}>
      <SpaceBetween size="l">
        {/* Shipment ID Input Section */}
        <Container
          header={
            <Header variant="h2" info={<Box>Info</Box>}>
              Shipment ID
            </Header>
          }
        >
          <Box>
            <p style={{ fontSize: '14px', color: '#424650', marginBottom: '12px' }}>
              Select an ID type and enter the shipment ID(s)
            </p>
            <div style={{ display: 'flex', gap: '12px', alignItems: 'center' }}>
              <div style={{ width: '160px' }}>
                <Select
                  selectedOption={{ label: "ARN", value: "arn" }}
                  options={[
                    { label: "ARN", value: "arn" },
                    { label: "FBA Shipment ID", value: "fba" },
                    { label: "PRO", value: "pro" }
                  ]}
                />
              </div>
              <div style={{ flex: 1 }}>
                <Input
                  placeholder="Enter ARN(s), separated by commas"
                  type="text"
                />
              </div>
              <Button variant="primary">Add</Button>
            </div>
          </Box>
        </Container>

        <ShipmentCards highlightedShipment={highlightedShipment} />

        <Container
          header={
            <Header variant="h2" info={<Box>Info</Box>}>
              PO delivery windows
            </Header>
          }
        >
          <Table
            columnDefinitions={columnDefinitions}
            items={deliveryWindowsData}
            loadingText="Loading delivery windows"
            sortingDisabled
            empty={
              <Box textAlign="center" color="inherit">
                <b>No delivery windows</b>
                <Box padding={{ bottom: "s" }} variant="p" color="inherit">
                  No delivery windows to display.
                </Box>
              </Box>
            }
          />
        </Container>
      </SpaceBetween>

      {/* Tooltip */}
      {tooltip.visible && (
        <div
          style={{
            position: 'fixed',
            left: tooltip.x,
            top: tooltip.y,
            transform: 'translateX(-50%) translateY(-100%)',
            backgroundColor: '#232f3e',
            color: 'white',
            padding: '8px 12px',
            borderRadius: '6px',
            fontSize: '12px',
            fontWeight: '500',
            whiteSpace: 'nowrap',
            zIndex: 1000,
            pointerEvents: 'none',
            boxShadow: '0 2px 8px rgba(0, 0, 0, 0.15)',
            opacity: 1,
            transition: 'opacity 0.2s ease'
          }}
        >
          {tooltip.content}
          <div
            style={{
              position: 'absolute',
              top: '100%',
              left: '50%',
              transform: 'translateX(-50%)',
              width: 0,
              height: 0,
              borderLeft: '6px solid transparent',
              borderRight: '6px solid transparent',
              borderTop: '6px solid #232f3e'
            }}
          />
        </div>
      )}
    </div>
  );
};

export default Option4Interactive;
